import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import SupplierUpdateProfileForm from "./updateProfileForm";
import SupplierContacts from "./indexContact";
import SupplierAddresses from "./indexAddress";
import SupplierOpeningBalance from "./indexOpeningBalance";

import { Tabs, Spin } from "antd";

import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/supplier/supplier/actions";


const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/suppliers";

class UpdateSupplier extends Component {
  state = {
    id_supplier: null,
    url: null,
  }

  componentDidMount = () => {
    const { editSupplier } = this.props;
    const { id } = this.props.match.params;
    editSupplier(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.supplier.error) {
      notification("error", this.props.supplier.error);
      this.props.clearError();
    }
    if (this.props.supplier.success) {
      notification("success", this.props.supplier.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateSupplier } = this.props;
    const { id } = this.props.match.params;
    updateSupplier(id, value);
  };


  handleSubmitAddContact = value => {
    const { storeSupplierContact } = this.props;
    const { id } = this.props.match.params;
    storeSupplierContact({ ...value, id_supplier: id })
  }

  handleSubmitUpdateContact = value => {
    const { updateSupplierContact } = this.props;
    updateSupplierContact(value.id_supplier_contact, value);
  }

  handleSubmitAddAddress = value => {
    const { storeSupplierAddress } = this.props;
    const { id } = this.props.match.params;
    storeSupplierAddress({ ...value, id_supplier: id })
  }

  handleSubmitUpdateAddress = value => {
    const { updateSupplierAddress } = this.props;
    updateSupplierAddress(value.id_supplier_address, value);
  }

  handleSubmitAddOpeningBalance = value => {
    const { storeSupplierOpeningBalance } = this.props;
    const { id } = this.props.match.params;
    storeSupplierOpeningBalance({ ...value, id_supplier: id })
  }

  handleSubmitUpdateOpeningBalance = value => {
    const { updateSupplierOpeningBalance } = this.props;
    updateSupplierOpeningBalance(value.id_supplier_opening_balance, value);
  }



  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="suppliers.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>


          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="suppliers.tab.supplier" />}
              key="1"
            >
              {this.props.supplier.loading ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (

                  <SupplierUpdateProfileForm
                    auth={this.props.auth}
                    submit={this.handleSubmit}
                  />
                )
              }
            </TabPane>
            <TabPane tab={<IntlMessages id="suppliers.tab.contact" />}
              key="2"
            >
              {
                this.props.supplier.loading ? (
                  <div className="spinStyle">
                    <Spin size="large" />
                  </div>
                ) : (

                    <SupplierContacts
                      auth={this.props.auth}
                      handleSubmitAddContact={this.handleSubmitAddContact}
                      handleSubmitUpdateContact={this.handleSubmitUpdateContact}
                    />
                  )
              }
            </TabPane>
            <TabPane tab={<IntlMessages id="suppliers.tab.address" />}
              key="3"
            >
              {
                this.props.supplier.loading ? (
                  <div className="spinStyle">
                    <Spin size="large" />
                  </div>
                ) : (

                    <SupplierAddresses
                      auth={this.props.auth}
                      handleSubmitAddAddress={this.handleSubmitAddAddress}
                      handleSubmitUpdateAddress={this.handleSubmitUpdateAddress}
                    />
                  )
              }
            </TabPane>
            <TabPane tab={<IntlMessages id="suppliers.tab.openingBalance" />}
              key="4"
            >
              {
                this.props.supplier.loading ? (
                  <div className="spinStyle">
                    <Spin size="large" />
                  </div>
                ) : (
                    <SupplierOpeningBalance
                      auth={this.props.auth}
                      handleSubmitAddOpeningBalance={this.handleSubmitAddOpeningBalance}
                      handleSubmitUpdateOpeningBalance={this.handleSubmitUpdateOpeningBalance}
                    />
                  )
              }
            </TabPane>
          </Tabs>

        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, supplier: state.supplier };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateSupplier);
