import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Row, Modal } from "antd";

// const { Option } = Select;
// const { TextArea } = Input;

class ModalAddContact extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { loading } = this.props
    // const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="addContact" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={<IntlMessages id="button.submit"></IntlMessages>}
        cancelText={<IntlMessages id="button.cancel"></IntlMessages>}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}

      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="supplierContacts.add.name" />}
            >
              {getFieldDecorator("supplier_contact_name", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
                })
                (<Input 
                  //autosize={{ minRows: 2 }} 
                  //disabled={used ? true : false} 
                  />
                )
              }
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="supplierContacts.add.jobTitle" />}
            >
              {getFieldDecorator("job_title", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
                })
                (<Input 
                  //autosize={{ minRows: 2 }} 
                  //disabled={used ? true : false} 
                  />
                )
              }
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="phone" />}
            >
              {getFieldDecorator("phone", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 15,
                    message: <IntlMessages id="maxLength15" />
                  },
                  {
                    pattern: "^[0-9]*$",
                    message: <IntlMessages id="invalidPhone" />
                  }
                ]
                })
                (<Input 
                  //autosize={{ minRows: 2 }} 
                  //disabled={used ? true : false} 
                  />
                )
              }
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="email" />}
            >
              {getFieldDecorator("email", {
                rules: [
                  {
                    type: "email",
                    message: <IntlMessages id="invalidEmail" />
                  }
                ]
              })(<Input allowClear={true} />)}
            </Form.Item>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddContact = Form.create({ name: "modal_add_contact" })(
  ModalAddContact
);

export default WrappedModalAddContact;
